	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Fun Fun Fun","date":"17 Jun 2007","slug":"/2007-06-17-fun-fun-fun/","tags":"Y2007"},"html":"<p>My wife's family decided a few years ago to combine all their birthday celebrations into 3 combined gatherings. All birthdays are pretty much clustered in November, February and May/June. So November we go to her older brother, in February to her younger brother, and in June they all come to us. That day is today, and we will have 14 guests in Chateau Lüllau.</p>\n<p>We always plan an outdoor celebration, but our party is invariably on the coldest and wettest day of the summer. We're used to putting on sweaters and grilling in the carport. We may have better luck today... the temperature will be cool (18°C) rather than cold, and the showers will be scattered (rather than intense like the past couple of days). Since we have been occupied by other things the past few weeks, we didn't really start preparing until yesterday. </p>\n<p>This year we have a new complication... Christopher came down with the stomach flu overnight, and is still feeling tough this morning. He's generally very healthy, he hasn't been sick for a year and a half. But now we have a child to comfort and extra loads of laundry along with the other preparations. It will be a long day... hopefully we can have *some* fun.</p>"},"next":{"fields":{"title":"Caucasian","date":"17 Jun 2007","slug":"/2007-06-17-caucasian/"}},"previous":{"fields":{"title":"24/7","date":"18 Jun 2007","slug":"/2007-06-18-247/"}}},{"node":{"fields":{"title":"Caucasian","date":"17 Jun 2007","slug":"/2007-06-17-caucasian/","tags":"Y2007"},"html":"<p>In the \"you're never too old to learn something new\" department, I hadn't realized that the word caucasian to describe race come from an <a href=\"http://www.english.upenn.edu/Projects/knarf/People/blumen.html\">18th century German scientist</a>, and isn't used anymore outside of the US. Thanks to <a href=\"http://usaerklaert.wordpress.com/2007/06/11/kurz-erklart-wo-kommen-die-ganzen-kaukasier-her/\">USA Erklärt</a>, now I know.</p>\n<p>I've grown weary of the whole American obsession with race, and I've gotten in the habit of ticking \"other\" when filling out forms that ask for my race (or, if I'm feeling ornery, writing in \"none of your business\").</p>"},"next":{"fields":{"title":"Paper Weight","date":"15 Jun 2007","slug":"/2007-06-15-paper-weight/"}},"previous":{"fields":{"title":"Fun Fun Fun","date":"17 Jun 2007","slug":"/2007-06-17-fun-fun-fun/"}}},{"node":{"fields":{"title":"Paper Weight","date":"15 Jun 2007","slug":"/2007-06-15-paper-weight/","tags":"Y2007"},"html":"<p>I know I said I wouldn't blog numbers, I but think Frauke will forgive me for this one... her franchise contract arrived today by courier. With 4 copies of everything, with all appendices and conditions, the weight of the package was *2.6 kg*. </p>\n<p>Since my name does not appear in the contract or in the papers for the GmbH, I could only watch as Frauke initialed and signed all the pages.</p>"},"next":{"fields":{"title":"We Got The Money","date":"14 Jun 2007","slug":"/2007-06-14-we-got-the-money/"}},"previous":{"fields":{"title":"Caucasian","date":"17 Jun 2007","slug":"/2007-06-17-caucasian/"}}}],"pathPrefix":"","first":false,"last":false,"index":120,"pageCount":1101}}

